import db from 'libs/db';
import React from 'react';

const Footer = ({
  next,
  navigation,
  state,
  saveData,
  nextDisabled = false,
  buttonText
}) => {

  const onNextPress = async () => {
    if(!nextDisabled){
      await db.get(db.model.createGoal).then(async (p)=>{
        console.log({...p,...saveData})
        await db.set(db.model.createGoal,JSON.stringify({...p,...saveData}))
      })
      navigation.push({ pathname: next, state })
    }
  };

  return (
    <div className='footer'>
      <button
        className='solidButton'
        style={{ opacity: nextDisabled ? 0.4 : 1 }}
        onClick={() => onNextPress()}
      >
        {buttonText}
      </button>
    </div>
  );
};

export default Footer;
