import React from 'react';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import ProgressBar from 'react-bootstrap/ProgressBar';
import Footer from './Footer';
import db from 'libs/db';
export default function GoalName(props) {

	const [goalName, setGoalName] = React.useState('');
	const [nextDisabled, setNextDisabled] = React.useState(true);
	const STATE = props.location.state

	// const capitalGainsTax = 10;
	const tr = STATE.goalDue <= 3 ? 6.2 + (STATE.goalDue - 1) : STATE.goalDue <= 5 ? 9 : 12
	const inflation = STATE.type === 'education' ? 10 : STATE.type === 'house' ? 8 : STATE.type === 'custom' ? STATE.inflation:6
	// const FV = parseInt(STATE.presentCost * Math.pow((1 + (inflation+capitalGainsTax) / 100), STATE.goalDue))
	// const adjustedFV = FV - STATE.investToday * Math.pow((1+tr/100), STATE.goalDue)

	React.useEffect(async ()=>{
		await db.get(db.model.createGoal).then(async (p)=>{
			if(p && p.goalName!==undefined){
				setGoalName(p.goalName)
				delete p.sip
				delete p.oneTime
				console.log(p)
				await db.set(db.model.createGoal,JSON.stringify(p))
			} else {
				setGoalName("")
			}
		})
	},[])

	const checkValidation = () => {
		if (goalName.length > 2) {
			setNextDisabled(false);
		} else {
			setNextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	}, [goalName])

	if (STATE === undefined) {
		props.history.push('/Goals')
		return <></>
	}
	return (
		<div className='mainContainer'>
			<MainHeader
				header={STATE.header}
				navigation={props.history}
			/>
			<ProgressBar now={100} className="progress-bar-custom" />
			<div className='dataContainer'>
				<div className="bodyTextDark" style={{ marginTop: "6%" }}>{STATE.goalNameScreen.question}</div>
				<input
					type="text"
					id="goalName"
					className='normalInput'
					autoFocus
					placeholder={STATE.goalNameScreen.placeholder}
					value={goalName}
					onChange={(n) => {
						setGoalName(n.target.value);
					}}
				/>
			</div>
			<Footer
				navigation={props.history}
				next='/Goals/goal-details'
				state={{ ...STATE, goalName}}
				saveData = {{ goalName }} 
				buttonText="Next"
				nextDisabled={nextDisabled}
			/>
		</div>
	)
}
